import React, { Suspense, useEffect, useRef, useMemo, useState } from "react"
import './PageStyles.css';

import ProjectsHeader1 from '../assets/images/projects1.png'
import ProjectsHeader2 from '../assets/images/projects2.png'

const Projects = () => {
  const [state, setState] = useState(0);

  useEffect(() => {
    const getRandomInt=(max)=> {
      return Math.floor(Math.random() * Math.floor(max));
    }
    setState(getRandomInt(2))
    // const timer = setTimeout(() => setState(null), 2000);
    // return () => {
    //   clearTimeout(timer);
    // };
  }, []);

  const ChangeTitle = () => {
    if (state==0) {
      return (
        <img className="Page-title-image" src={ProjectsHeader1} alt="header1"></img>
      )
    } else if (state==1) {
      return (
        <img className="Page-title-image" src={ProjectsHeader2} alt="header2"></img>
      )
    } else return null;
  }

  return (
    <div className="Page-body">
      <div className="Page-content-container">
        {/* <ChangeTitle /> */}
        <div className="Page-title-container">

          <h1 className="Page-title">WEB-SYNTH</h1>
          <p>A 3x oscillator polyphonic synthesizer using Web Audio API, written in React.</p>
          <p>{"links => "}(<a className="Page-link" href="https://github.com/yungrazr/web-synth">github</a>, <a className="Page-link" href="https://dvegera.dev/web-synth/">live</a>)</p>
          <h1 className="Page-title2">WEB-SYNTH</h1>
          <br></br>

          <h1 className="Page-title">TAPTAP-TEMPO</h1>
          <p>A simple BPM tapper and metronome application built using Flutter for web and mobile.</p>
          <p>{"links => "}(<a className="Page-link" href="https://github.com/yungrazr/taptap-tempo">github</a>, <a className="Page-link" href="https://dvegera.dev/taptap-tempo/">live</a>)</p>
          <h1 className="Page-title2">TAPTAP-TEMPO</h1>

        </div>

      </div>
    </div>
  );
}

export default Projects;