import React, { useState, useEffect } from 'react';
import './PageStyles.css';

import AboutMeHeader1 from '../assets/images/aboutme1.png'
import AboutMeHeader2 from '../assets/images/aboutme2.png'

const About = () =>{
  const [state, setState] = useState(0);

  useEffect(() => {
    const getRandomInt=(max)=> {
      return Math.floor(Math.random() * Math.floor(max));
    }
    setState(getRandomInt(2))
  }, []);

  const ChangeTitle = () => {
    if (state==0) {
      return (
        <img className="Page-title-image" src={AboutMeHeader1} alt="header1"></img>
      )
    } else {
      return (
        <img className="Page-title-image" src={AboutMeHeader2} alt="header2"></img>
      )
    }
  }

  return (
    <div className="Page-body">
      <div className="Page-content-container">
        {/* <ChangeTitle /> */}
        <div className="Page-title-container">
          <h1 className="Page-title">DANIEL VEGERA</h1>
          <br></br>
          <p>Front-end mobile and web developer.</p>
          <br></br>
          <p>{"links => "}(<a className="Page-link" href="https://github.com/yungrazr">github</a>, <a className="Page-link" href="https://www.linkedin.com/in/daniel-vegera/">linkedin</a>)</p>
          <p>{"email => "}hello@dvegera.dev</p>
          <br></br>
          <h1 className="Page-title2">DANIEL VEGERA</h1>
        </div>
      </div>
    </div>
  );
}

export default About;