import React, { Suspense, useState, useEffect } from 'react';
import './App.css';
import About from './pages/about';
import Projects from './pages/projects';
import Home from './pages/home';
import ThreeCanvas from './pages/background'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  HashRouter
} from "react-router-dom";

import Sun from "./assets/images/sun.png"
import Moon from "./assets/images/moon.png"

const Spinner = () => {
  return "";
};

function App() {

  const [invert, setInvert] = useState(false);
  const classnames = [
    'App',
    invert && 'App--inverted'
  ].filter(Boolean);

  return (
    <HashRouter basename='/'>
      <div className={classnames.join(' ')} id="App">
        <ThreeCanvas classname="Three-canvas" />
        <Switch>
          <Route path="/about">
            <div className="App-header">
              <h1 className={"App-category-title"}><Link to="/">Home</Link></h1>
              <h1 className={"App-category-title"}><Link to="/projects">Projects</Link></h1>
              <h1 className={"App-category-title-clicked"}>About</h1>
              <div className={"App-invert"} >
              <img src={invert ? Sun : Moon} onClick={()=>setInvert(!invert)}></img>
              </div>
            </div>
            <div className="App-body">
              <About />
            </div>
          </Route>
          <Route path="/projects">
            <header className="App-header">
              <h1 className={"App-category-title"}><Link to="/">Home</Link></h1>
              <h1 className={"App-category-title-clicked"}>Projects</h1>
              <h1 className={"App-category-title"}><Link to="/about">About</Link></h1>
              <div className={"App-invert"} >
              <img src={invert ?  Sun : Moon} onClick={()=>setInvert(!invert)}></img>
              </div>
            </header>
            <div className="App-body">
              <Projects />
            </div>
          </Route>
          <Route exact path="/">
            <header className="App-header">
              <h1 className={"App-category-title-clicked"}>Home</h1>
              <h1 className={"App-category-title"}><Link to="/projects">Projects</Link></h1>
              <h1 className={"App-category-title"}><Link to="/about">About</Link></h1>
              <div className={"App-invert"} >
              <img src={invert ?  Sun : Moon} onClick={()=>setInvert(!invert)}></img>
              </div>
            </header>
            <div className="App-body">
              <Home />
            </div>
          </Route>
        </Switch>
        <div className="App-footer">
          <h1 className="App-category-title">© 2021</h1>
        </div>
      </div>
    </HashRouter>
  );
}
export default App;