import React, { useRef, useState, useEffect, useMemo } from 'react'
import * as THREE from 'three'
import { Canvas, useFrame, useThree, extend } from 'react-three-fiber'
import { FilmPass } from 'three/examples/jsm/postprocessing/FilmPass'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass'
import { GlitchPass } from 'three/examples/jsm/postprocessing/GlitchPass'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer'
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass'

extend({ EffectComposer, FilmPass, RenderPass, GlitchPass, UnrealBloomPass})

function Plane(props) {
  // This reference will give us direct access to the mesh
  const mesh = useRef()

  return (
    <mesh
      {...props}
      ref={mesh}>
      <planeGeometry args={[150, 150, 15]} />
      <meshStandardMaterial color={"red"} />
    </mesh>
  )
  }

function Box(props) {
  // This reference will give us direct access to the mesh
  const mesh = useRef()

  const {
    viewport,
  } = useThree()
  const { width, height } = viewport

  // Rotate mesh every frame, this is outside of React without overhead
  useFrame(() => {
    mesh.current.rotation.x = mesh.current.rotation.y += 0.01
  })
  return (
    <mesh
      {...props}
      ref={mesh}>
      {width < 2 ? <boxBufferGeometry args={[height*6, height*6, height*6]} /> : <boxBufferGeometry args={[width*4, width*4, width*4]} />}
      <meshStandardMaterial color={"white"} />
    </mesh>
  )
}

function MouseLight(){
  const {
    mouse,
  } = useThree()
  return(
    <directionalLight color={"white"} intensity={1.0} position={[mouse.x, mouse.y, 100]} />
  )
}

function Effects() {
    const composer = useRef()
    const { scene, gl, size, camera } = useThree()
    const aspect = useMemo(() => new THREE.Vector2(512, 512), [])
    useFrame(() => composer.current.render(), 1);
    return (
        <effectComposer ref={composer} args={[gl]}>
        <renderPass attachArray="passes" scene={scene} camera={camera} />
        <unrealBloomPass attachArray="passes" args={[aspect, 2, 1, 0]} />
        <filmPass args={[0.5,0.2,100,true]} attachArray="passes" renderToScreen />
        </effectComposer>
      )
}   

function ThreeCanvas() {
    return (
        <Canvas
        camera={{ fov: 100, position: [0, 0, 1] }}>
          <directionalLight color={"white"} intensity={1.0} position={[0, 0, 100]} />
          <Box position={[0, 0, -20]} />
          <Effects />
        </Canvas>
    )
}
export default ThreeCanvas;